
hr {
  color: #000;
  background-color: #000;
  height: 5px;
}
.section {
  margin-bottom: 42px;
}
.option, form input, form a {
  display: inline-block;
  width: calc(25% - 18px);
  cursor: pointer;
  margin: 7px;
  padding: 7px;
  text-align: center;
  border: 2px solid;
  box-sizing: border-box;
  border-radius: 0;
}
.option {
  font-size: 28px;
}
.option:hover {
    background-color: #acdfac;
}
form input {
  font-size: 21px;
  line-height: 28px;
}
form a {
  color: #fefefe;
  background-color: #000;
  font-size: 28px;
  line-height: 32px;
  text-decoration: none;
}
footer {
  margin-top: 63px;
  padding: 7px;
  margin-bottom: 0;
  text-align: center;
}
@media all and (max-width: 1024px) {
.option, form input, form a {
    display: block;
    width: 100%;
}
}



